@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
}

.offscreen,
.hide {
    display: none;
}

/* Header section */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100vw;
    padding: 16px;
}

#logo {
    text-decoration: none;
    font-weight: 700;
    font-size: 28px;
    color: black;
}

/* Homepage with overview of posts */

.post-container {
    width: 100%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.post p,
.post-detail p {
    white-space: pre-wrap;
}

.post,
.post-detail {
    border: solid 1px lightgrey;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 900px;
    box-shadow: 0px 0px 3px 1px lightgray;
}

.post p:nth-child(2),
.post-detail p:nth-child(2) {
    font-size: 16px;
    color: darkgrey;
    font-weight: 500;
    padding-bottom: 10px;
}

/* Detailed post section */

.post-detail h2 {
    margin-bottom: 10px;
}

.post-detail {
    margin-top: 10px;
}

.post-long {
    padding-bottom: 30px;
    border-bottom: solid 1px lightgrey;
}

/* Add comment section */

.add-comment {
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding-bottom: 15px;
    border-bottom: solid 1px lightgrey;
}

#name,
#comment {
    padding: 4px;
    margin-bottom: 5px;
}

#name {
    max-width: 200px;
}

#comment {
    resize: none;
    height: 100px;
}

button {
    width: fit-content;
    padding: 2px 20px;
    border: none;
    border-radius: 4px;
    color: white;
    background-color: black;
    font-weight: 700;
    font-size: 16px;
    cursor: pointer;
}

/* Comment section */

.comment {
    margin-top: 10px;
    border-bottom: solid 1px lightgrey;
}

.comment p:nth-child(1) {
    font-weight: 700;
    font-size: 14px;
    margin-bottom: 4px;
}

.comment p:nth-child(2) {
    font-weight: 400;
    font-size: 14px;
    color: black;
}

.comment:last-child {
    border: none;
}

#no-comments {
    margin-top: 10px;
}
